.card {
    border-radius: 10px;
    overflow: hidden;
}
.discount {
    position: absolute;
    top: 10px;
    left: 10px;
    background: orange;
    color: white;
    padding: 5px 10px;
    font-weight: bold;
    border-radius: 5px;
}
.card img {
    height: 200px;
    object-fit: cover;
    transition: transform 0.5s ease-in-out;
}

.card img:hover{

    transform: scale(1.1);
}

.btn{
    background-color: black;
    border: 2px solid black;
}
.btn:hover{
    background-color: blueviolet;
    border: 2px solid blueviolet;
}